



























































































@import "../../styles/variables";

.infl-prdgm-tbl {
  display: table;
  border-top: 1px solid gray;
  border-left: 1px solid gray;
  margin-bottom: 30px;
}

.infl-prdgm-tbl__row {
  display: table-row;
}

.infl-prdgm-tbl__cell {
  display: table-cell;
  padding: 2px 5px;
  border-right: 1px solid gray;
  border-bottom: 1px solid gray;
}

.infl-prdgm-tbl__cell--label {
  font-weight: 700;
}
