/*
 Calculates a size of text elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-text-size` custom prop.
 */
/*
 Calculates a size of UI elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-ui-size` custom prop.
 */
.alpheios-wordlist-sorting {
  padding-bottom: 5px;
  border-bottom: 1px solid var(--alpheios-color-neutral-dark);
}
.alpheios-wordlist-sorting .alpheios-worditem__icon {
  width: 22px;
  margin: 5px;
}
.alpheios-wordlist-sorting .alpheios-worditem__targetWord {
  width: 28%;
}
.alpheios-wordlist-sorting .alpheios-worditem__targetWord svg {
  width: 22px;
  height: 22px;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  padding: 7px;
  fill: var(--alpheios-word-list-sorting-link-color);
  box-sizing: content-box;
}
.alpheios-wordlist-sorting .alpheios-worditem__targetWord svg.alpheios-icon-active {
  fill: var(--alpheios-word-list-sorting-link-color-hover);
}

.alpheios-layout-compact .alpheios-wordlist-sorting .alpheios-worditem__targetWord {
  width: 25%;
}